@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300&display=swap');


.btns{
    width: 80px;
    padding: 2% 2% 2% 2%;
    border: none;
    background: whitesmoke;
    border-radius: 5px;
    box-shadow: 0 4px 30px rgba(220, 215, 215, 0.514);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    cursor: pointer;
}

:hover.btns {
    border: none;
    background: none;
    background: rgba(255, 255, 255, 0);
    border-radius: 5px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
}

@media (max-width: 800px) {

    .btns{
        width: 20%;
        padding: 2% 1%;
        margin: 1%;
        border: none;
        background: whitesmoke;
        border-radius: 5px;
        box-shadow: 0 4px 30px rgba(220, 215, 215, 0.514);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        cursor: pointer;
        font-size: small;
        height: 5vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    

}